export default "import * as React from 'react';\r\nimport Box from '@mui/material/Box';\r\nimport Paper from '@mui/material/Paper';\r\n\r\nexport default function SimplePaper() {\r\n  return (\r\n    <Box\r\n      sx={{\r\n        display: 'flex',\r\n        flexWrap: 'wrap',\r\n        '& > :not(style)': {\r\n          m: 1,\r\n          width: 128,\r\n          height: 128,\r\n        },\r\n      }}\r\n    >\r\n      <Paper elevation={0} />\r\n      <Paper />\r\n      <Paper elevation={3} />\r\n    </Box>\r\n  );\r\n}\r\n";